<template>
  <div name="loginWindow" class="loginWindow-wrap">
    <div v-if="status">
      <div class="title">
        <font>登录</font>
      </div>
      <div class="form">
        <div style="margin: 40px 0 0 0">
          <div>
            <font>账号</font>
            <input
              type="text"
              placeholder="请输入您的账号"
              v-model="loginForm.account"
            />
          </div>
          <div style="margin: 20px 0 0 0">
            <font>密码</font>
            <input
              type="password"
              placeholder="请输入您的密码"
              v-model="loginForm.password"
            />
          </div>
        </div>
      </div>
      <div class="register" @click="status = !status">
        <font>还未注册？点击此处注册！</font>
      </div>
      <div class="submit" @click="login()">
        <div>
          <font>登录</font>
        </div>
      </div>
    </div>

    <div v-if="!status">
      <div class="title">
        <font>注册</font>
      </div>
      <div class="form">
        <div style="margin: 40px 0 0 0">
          <div>
            <font>用户名</font>
            <input
              type="text"
              placeholder="请填写您的用户名"
              v-model="registerForm.name"
            />
          </div>
          <div style="margin: 20px 0 0 0">
            <font>账号</font>
            <input
              type="text"
              placeholder="请填写您的账号"
              v-model="registerForm.account"
            />
          </div>
          <div style="margin: 20px 0 0 0">
            <font>密码</font>
            <input
              type="password"
              placeholder="请输入您的密码"
              v-model="registerForm.password"
            />
          </div>
        </div>
      </div>
      <div class="register" @click="status = !status">
        <font>返回</font>
      </div>
      <div class="submit" @click="register()">
        <div>
          <font>注册</font>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { userLoginAPI, userRegisterAPI } from "@/api/user";
export default {
  name: "loginWindow",
  data() {
    return {
      loginForm: {
        account: "",
        password: "",
      },
      registerForm: {
        name: "",
        account: "",
        password: "",
      },
      user_list: [],
      status: true,
    };
  },
  methods: {
    login() {
      if (!this.loginForm.account || !this.loginForm.password) {
        this.$notify({
          title: "账号/密码未填好！",
          type: "error",
        });
      } else {
        userLoginAPI({ ...this.loginForm }).then((res) => {
          if (res.data.result_code === 1) {
            if (res.data.result_length === 0) {
              this.$notify({
                title: "登录失败！",
                message: "该账号还没有注册哦，请先注册！",
                type: "error",
              });
            } else {
              this.$notify({
                title: "登录失败！",
                message: res.data.result_msg,
                type: "error",
              });
            }
          } else {
            localStorage.setItem("token", res.data.token);
            res.data.user_info.major = res.data.user_info.major.split(",");
            localStorage.setItem(
              "user_info",
              JSON.stringify(res.data.user_info)
            );
            localStorage.setItem(
              "keywords_list",
              JSON.stringify(res.data.keywords_list)
            );
            localStorage.setItem(
              "article_labels",
              JSON.stringify(res.data.article_labels)
            );
            this.$notify({
              title: "登录成功！",
              message: `${res.data.user_info.name}，欢迎回来！`,
              type: "success",
            });
            this.$router.push({ name: "home" });
          }
        });
      }
    },
    register() {
      if (
        this.registerForm.name &&
        this.registerForm.account &&
        this.registerForm.password
      ) {
        userRegisterAPI(this.registerForm).then((res) => {
          if (res.data.result_code === 1) {
            this.$notify({
              title: "注册失败！",
              message: res.data.result_msg,
              type: "error",
            });
          } else {
            this.status = !this.status;
            this.$notify({
              title: "注册成功！",
              message: "快快去登录吧~",
              type: "success",
            });
          }
        });
      } else {
        this.$notify({
          title: "注册必填项未填好！",
          type: "error",
        });
      }
    },
  },
};
</script>

<style scoped>
.loginWindow-wrap {
  padding: 20px;
  margin: 200px auto;
  width: 300px;
  background-color: #fff;
  border-radius: 50px;
}
.title {
  width: 100%;
  display: flex;
  justify-content: center;
}
.title font {
  font-family: SourceHanSansCN-Medium;
  font-size: 24px;
  font-weight: 500;
  color: #3d3d3d;
}
.form {
  width: 100%;
  display: flex;
  justify-content: center;
}
.form font {
  font-family: SourceHanSansCN-Medium;
  font-size: 18px;
  font-weight: 500;
  line-height: 17px;
  color: #3d3d3d;
}
.form input {
  width: 200px;
  height: 40px;
  border-radius: 10px;
  border: 0.5px #ff8200 solid;
  background-color: #f5f5f5;
  margin: 0 0 0 20px;
}
.submit {
  width: 100%;
  margin: 30px 0 0 0;
  display: flex;
  justify-content: center;
}
.submit div {
  width: 70px;
  height: 40px;
  background-color: #ff820088;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
  cursor: pointer;
  transition: all 0.3s;
}
.submit div:hover {
  background-color: #ff8200;
}
.submit div font {
  font-family: SourceHanSansCN-Medium;
  font-size: 15px;
  font-weight: 500;
  color: #ffffff;
}
.register {
  width: 100%;
  margin: 10px 0 0 0;
  display: flex;
  justify-content: center;
  cursor: pointer;
}
.register font {
  font-family: SourceHanSansCN-Medium;
  font-size: 10px;
  font-weight: 500;
  line-height: 17px;
  color: #3d3d3d;
  transition: all 0.2s;
}
.register:hover font {
  color: #ff8200;
}
</style>